'use client';

import React, { useState, useEffect } from 'react';
import Link from 'next/link';
import { motion, AnimatePresence } from 'framer-motion';
import { Menu, X, ArrowRight, Github } from 'lucide-react';
import { clsx } from 'clsx';

const Navbar: React.FC = () => {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 20);
    };
    window.addEventListener('scroll', handleScroll, { passive: true });
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  return (
    <>
      <motion.nav 
        initial={{ y: -100 }}
        animate={{ y: 0 }}
        transition={{ duration: 0.6 }}
        className="fixed top-6 left-0 right-0 z-50 flex justify-center px-4 pointer-events-none"
      >
        <div className={clsx(
          "pointer-events-auto flex items-center justify-between gap-8 px-6 py-3 rounded-full transition-all duration-300 w-full max-w-5xl",
          isScrolled 
            ? "bg-black/60 backdrop-blur-xl border border-white/10 shadow-lg shadow-black/20" 
            : "bg-transparent border border-transparent"
        )}>
          
          {/* Logo */}
          <Link href="/" className="flex items-center gap-3 group">
            <div className="relative w-9 h-9 flex items-center justify-center">
               <div className="absolute inset-0 bg-primary/40 blur-lg rounded-full opacity-0 group-hover:opacity-100 transition-opacity duration-500"></div>
               <div className="relative w-9 h-9 rounded-xl bg-gradient-to-br from-white to-neutral-400 flex items-center justify-center text-black font-bold text-lg shadow-xl">
                 Z
               </div>
            </div>
            <span className="text-lg font-bold tracking-tight text-white group-hover:text-primary transition-colors hidden sm:block">Zyntrakt</span>
          </Link>

          {/* Desktop Links */}
          <div className="hidden md:flex items-center gap-1">
            {[
              { label: 'Features', href: '#features' },
              { label: 'How it works', href: '#how-it-works' },
              { label: 'Pricing', href: '#pricing' }
            ].map((link) => (
              <Link 
                key={link.label}
                href={link.href} 
                className="px-4 py-2 text-sm font-medium text-neutral-400 hover:text-white rounded-full transition-all hover:bg-white/5"
              >
                {link.label}
              </Link>
            ))}
          </div>

          {/* Actions */}
          <div className="flex items-center gap-3">
             <Link 
               href="https://github.com/zyntrakt" 
               target="_blank"
               className="hidden md:flex items-center justify-center w-9 h-9 rounded-full bg-white/5 border border-white/5 text-neutral-400 hover:text-white hover:bg-white/10 transition-colors"
             >
               <Github size={18} />
             </Link>
             <Link 
               href="https://chrome.google.com/webstore" 
               target="_blank"
               className="group flex items-center gap-2 px-5 py-2.5 text-sm font-semibold text-black bg-white rounded-full hover:bg-neutral-200 transition-all shadow-[0_0_20px_rgba(255,255,255,0.15)] hover:shadow-[0_0_25px_rgba(255,255,255,0.3)] active:scale-95"
             >
              <span className="hidden sm:inline">Install Extension</span>
              <span className="sm:hidden">Install</span>
              <ArrowRight size={14} className="group-hover:translate-x-0.5 transition-transform" />
            </Link>
            
            {/* Mobile Toggle */}
            <button 
              onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
              className="md:hidden p-2 text-white hover:bg-white/10 rounded-full transition-colors"
            >
              {isMobileMenuOpen ? <X size={20} /> : <Menu size={20} />}
            </button>
          </div>
        </div>
      </motion.nav>

      {/* Mobile Menu */}
      <AnimatePresence>
        {isMobileMenuOpen && (
          <motion.div 
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            exit={{ opacity: 0, scale: 0.95 }}
            transition={{ duration: 0.2 }}
            className="fixed inset-x-4 top-24 z-40 bg-neutral-900/95 backdrop-blur-xl border border-white/10 rounded-2xl p-6 md:hidden shadow-2xl"
          >
            <div className="flex flex-col gap-4">
              <Link href="#features" onClick={() => setIsMobileMenuOpen(false)} className="text-lg font-medium text-white py-2 border-b border-white/5">Features</Link>
              <Link href="#how-it-works" onClick={() => setIsMobileMenuOpen(false)} className="text-lg font-medium text-white py-2 border-b border-white/5">How it works</Link>
              <Link href="#pricing" onClick={() => setIsMobileMenuOpen(false)} className="text-lg font-medium text-white py-2 border-b border-white/5">Pricing</Link>
              <Link href="https://github.com/zyntrakt" target="_blank" className="text-lg font-medium text-white py-2">GitHub</Link>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </>
  );
};

export default Navbar;
