'use client';

import React, { useRef } from 'react';
import { motion, useScroll, useTransform } from 'framer-motion';
import { CheckCircle2 } from 'lucide-react';

const HowItWorks: React.FC = () => {
  const ref = useRef<HTMLDivElement>(null);
  const { scrollYProgress } = useScroll({
    target: ref,
    offset: ["start end", "end start"]
  });

  const steps = [
    {
      num: '01',
      title: 'Target',
      description: 'Navigate to any website. Open the Zyntrakt extension sidebar to begin scanning.'
    },
    {
      num: '02',
      title: 'Analyze',
      description: 'Our AI identifies recurring patterns, tables, and product grids automatically.'
    },
    {
      num: '03',
      title: 'Export',
      description: 'Review the preview. Download as clean JSON/CSV or copy Puppeteer scripts.'
    }
  ];

  return (
    <section id="how-it-works" ref={ref} className="py-32 bg-black relative overflow-hidden">
      
      <div className="max-w-7xl mx-auto px-6 relative z-10">
        <div className="text-center mb-24">
          <h2 className="text-4xl md:text-6xl font-bold mb-6 tracking-tighter">
            Extraction in <span className="text-white">3 Steps</span>
          </h2>
        </div>

        <div className="relative">
          {/* Connecting Beam */}
          <div className="absolute left-[19px] top-0 bottom-0 w-0.5 bg-white/10 hidden md:block">
            <motion.div 
              style={{ scaleY: scrollYProgress, transformOrigin: "top" }}
              className="absolute inset-0 bg-gradient-to-b from-blue-500 via-purple-500 to-blue-500 w-full"
            />
          </div>

          <div className="space-y-20">
            {steps.map((step, idx) => (
              <motion.div 
                key={idx}
                initial={{ opacity: 0, x: -20 }}
                whileInView={{ opacity: 1, x: 0 }}
                viewport={{ once: true, margin: "-100px" }}
                transition={{ duration: 0.6, delay: idx * 0.2 }}
                className="relative flex flex-col md:flex-row gap-8 md:gap-16 group"
              >
                {/* Step Marker */}
                <div className="hidden md:flex flex-col items-center">
                  <div className="w-10 h-10 rounded-full bg-black border-2 border-white/10 group-hover:border-primary/50 group-hover:bg-primary/10 transition-colors flex items-center justify-center z-10 shadow-[0_0_20px_rgba(0,0,0,1)]">
                    <div className="w-3 h-3 rounded-full bg-white/20 group-hover:bg-primary transition-colors" />
                  </div>
                </div>

                {/* Content */}
                <div className="flex-1 p-8 rounded-2xl bg-neutral-900/30 border border-white/5 hover:border-white/10 hover:bg-neutral-900/50 transition-all duration-500 group-hover:shadow-2xl">
                  <div className="flex items-start justify-between mb-4">
                    <span className="text-6xl font-bold text-white/5 font-mono absolute right-8 top-4 pointer-events-none group-hover:text-white/10 transition-colors">
                      {step.num}
                    </span>
                    <h3 className="text-2xl font-bold text-white group-hover:text-blue-200 transition-colors">{step.title}</h3>
                  </div>
                  <p className="text-neutral-400 text-lg leading-relaxed relative z-10">
                    {step.description}
                  </p>
                </div>
              </motion.div>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default HowItWorks;
